import { Component, EventEmitter, Input, Output, OnInit } from '@angular/core';
import { User} from '../shared/models/user/user.model'
import{FollowingService} from '../shared/services/following.service'
import { Router } from '@angular/router';
import { FollowerService } from '../shared/services/follower.service';

@Component({
  selector: 'app-following',
  templateUrl: './following.component.html',
  styleUrls: ['./following.component.css'],
  providers:[
    FollowingService,
    FollowerService
  ]
})
export class FollowingComponent implements OnInit {
  followingList:User[];
  followersCount:number;
  followingCount :number;
  followerList:User[];
 
  UserId:number;
  searchvalue:string;
  constructor(private followingservice:FollowingService,private router:Router, private followerservice:FollowerService) {
    if (sessionStorage.length <= 0)
    {
      this.router.navigateByUrl('/login');
    }
    this.followingList=[]
    this.searchvalue="";
    this.followersCount=0;
    this.followingCount =0;
    this.UserId=parseInt(sessionStorage.getItem('sessionuserid'))
   }

  ngOnInit() {
    this.followingservice.getFollowingById(this.UserId,this.searchvalue)
  .subscribe((event: any) => {
    this.followingList=event
    this.followingCount=this.followingList.length;
  });

  this.followerservice.getFollowerById(this.UserId)
  .subscribe((event: any) => {
   // console.log(event)
    this.followerList=event
    this.followersCount=this.followerList.length;

  });
  }

  @Output() onUnfollow :EventEmitter<number>=new EventEmitter<number>(); 
  //sends the unfollow request and calls service again to refresh the data
  unFollowById(idToUnfollow)
  {
    this.followingservice.unFollowById(this.UserId,idToUnfollow)
    .subscribe((event: any) => {
      this.followingservice.getFollowingById(this.UserId,this.searchvalue)
      .subscribe((event: any) => {
        this.followingList=event
        this.followingCount=this.followingList.length;
        this.onUnfollow.emit(this.followingCount);
      });
      
    });
  
}

logout()
{
  if(sessionStorage.length>=0)
  {
    sessionStorage.clear();
    window.alert("Successfully Logged Out");
        this.router.navigateByUrl('/login');
  }
}

}



