import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http'
import{FollowerFollowing} from '../models/follower-following/follower-following.model'
import { Router } from '@angular/router';

@Injectable()
export class FollowerService {
  BASE_URL='http://localhost:50554/api/Follower'

  constructor(private http:HttpClient,private router:Router) { 
    if (sessionStorage.length <= 0)
    {
      this.router.navigateByUrl('/login');
    }
  }
 // sends the request on webApi Controller to get the User Followers
  getFollowerById(UserId:number)
  {
   
    return this.http.get(this.BASE_URL+'/getFollowerById/'+UserId);
  }

}







