import { Component, OnInit } from '@angular/core';
import {User} from '../shared/models/user/user.model';
import{UserService} from '../shared/services/user.service'

import {CountryList} from '../shared/models/country/countylist.model'
import { Router } from '@angular/router';
import { Http, RequestOptions, Headers, Response } from '@angular/http';  
import { Observable } from 'rxjs/Rx';  
import { HttpModule } from '@angular/http';



@Component({
  selector: 'app-registration',
  templateUrl: './registration.component.html',
  styleUrls: ['./registration.component.css'],
  providers:[
    UserService
  ]
})
export class RegistrationComponent implements OnInit {
 
  countrylist=CountryList
  user:User=new User()
  
  isInserted:boolean=false;
//done
  private isUploadBtn: boolean = true;
  ImageUrl:string;
 
  
  constructor(private userservice: UserService,private router:Router,){
    this.ImageUrl="";
  }

  ngOnInit() { 
   
  
    
  }

//file upload event  
fileChange(event) {  
    let fileList: FileList = event.target.files;  
  if (fileList.length > 0) {  
  let file: File = fileList[0];  
  let formData: FormData = new FormData();  
  formData.append('uploadFile', file, file.name);  
  let headers = new Headers()  
  let options = new RequestOptions({ headers: headers });  
   this.userservice.uploadImage(formData,options).subscribe((event:any)=>{
     this.ImageUrl=event;
    
   })
  }  
 
  }  




registerUser(user){
  user.ImageUrl=this.ImageUrl
     this.userservice.postUser(user)
      .subscribe((event: any) => {
        
        this.isInserted=event;
        if(this.isInserted)
        {
        window.alert("Successfully Registered");
        this.router.navigateByUrl('/login');
        }
        else
        {
        window.alert("Either This Account Already Exists or Some Error Occured ..Try Again");
        this.router.navigateByUrl('/registration');
        }
     });
  
   
  }
}



