import { Component, OnInit } from '@angular/core';
import {UserService} from '../shared/services/user.service'
import{User} from '../shared/models/user/user.model'
import { Router } from '@angular/router';
import { Session } from 'protractor';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers:[
    UserService
  ]
})
export class LoginComponent implements OnInit {

  username:string=""
  user:User=new User();
  isWrongUser:boolean=false;
  constructor(private userservice: UserService,private router:Router) { }
  ngOnInit() {
  }
  //Gets the Response from the Database 
  //Creates User
  //If user is Invalid then Redirects to Login page
  loginUser(email:string, pass:string)
  {
    this.user.Email=email;
    this.user.Password=pass;
this.userservice.postcheckUser(this.user)
.subscribe((event: any) => {
  this.user=event;


  if(this.user.Email!==null && this.user.Password!==null && this.user.FName!==null)
  {
    sessionStorage.setItem('sessionuserid',JSON.stringify(this.user.Uid))
    sessionStorage.setItem('sessionemail',this.user.Email)
    sessionStorage.setItem('sessionpassword',this.user.Password)
    sessionStorage.setItem('sessionname',this.user.FName)
 this.router.navigateByUrl('/playground');
  }
  else
  {
  window.alert("Your User name or pass is wrong please Login Again");
  this.isWrongUser=true;
  this.router.navigateByUrl('/login');
  }
  

  })
}


sendOnSignUpPage()
  {
    this.router.navigateByUrl('/registration');
  }
}



