import { Injectable } from '@angular/core';
import {Analytics} from '../models/analytics/analytics'
import {HttpClient} from '@angular/common/http'
import{FollowerFollowing} from '../models/follower-following/follower-following.model'
import { Router } from '@angular/router';


@Injectable()
export class AnalyticsService {
  BASE_URL='http://localhost:50554/api/Analytics'
  constructor(private http:HttpClient,private router:Router) { 
    if (sessionStorage.length <= 0)
    {
      this.router.navigateByUrl('/login');
    }
  }
 
  //hit the request on webApi Controller to get the Analytics for the website
  getAnalytics()
  {
   
    return this.http.get(this.BASE_URL+'/getAnalytics')

  }



}








