import { Component, OnInit } from '@angular/core';
import{ Analytics} from '../shared/models/analytics/analytics'
import {AnalyticsService} from '../shared/services/analytics.service';
import{FollowerService} from '../shared/services/follower.service'
import { Router } from '@angular/router';
import { FollowingService } from '../shared/services/following.service';
import { User} from '../shared/models/user/user.model'
@Component({
  selector: 'app-analytics',
  templateUrl: './analytics.component.html',
  styleUrls: ['./analytics.component.css'],
  providers:[
    FollowerService,
    FollowingService,
    AnalyticsService
  ]
})
export class AnalyticsComponent implements OnInit {

  followerList:User[];
  UserId:number;
  followersCount:number;
  followingCount :number;
 followingList:User[];
 searchstring:string;
  analytics:Analytics;
  
  constructor(private analyticsService:AnalyticsService,private followerservice:FollowerService,private router:Router, private followingservice:FollowingService )
   { 
    if (sessionStorage.length <= 0)
    {
      this.router.navigateByUrl('/login');
    }
this.analytics=new Analytics();
this.followerList=[];
this.followersCount=0;
this.followingCount=0;
this.searchstring="";
this.UserId=parseInt(sessionStorage.getItem('sessionuserid'))

  }

  ngOnInit() {
    this.followerservice.getFollowerById(this.UserId)
    .subscribe((event: any) => {
    
      this.followerList=event
      this.followersCount=this.followerList.length;
      
     
    });
  
    this.followingservice.getFollowingById(this.UserId,this.searchstring)
    .subscribe((event: any) => {
     
      this.followingList=event
      this.followingCount=this.followingList.length;
  
    });
  


    this.analyticsService.getAnalytics()
    .subscribe((event: any) => {
    
     this.analytics=event;
     });


    
  }


logout()
{

    sessionStorage.clear();
    window.alert("Successfully Logged Out");
        this.router.navigateByUrl('/login');
}

}



