import { Component, OnInit ,ChangeDetectorRef  } from '@angular/core';
import { Route } from '@angular/compiler/src/core';
import { Router } from '@angular/router';
import {Tweet} from '../shared/models/tweet/tweet.model';
import {TweetService} from '../shared/services/tweet.service';
import{User} from '../shared/models/user/user.model';
import { UserService } from '../shared/services/user.service';
import { FollowingService } from '../shared/services/following.service';
import {UserFollower} from '../shared/models/user/userfollower.model'
import { FollowerService } from '../shared/services/follower.service';
import{Tweetlikedislike} from '../shared/models/tweet/tweetlikedislike';
import { MatDialogModule,MatDialog,MatDialogConfig } from '@angular/material';
import { MatFormFieldModule, MatInputModule } from '@angular/material';
import {TweetComponent} from '../tweet/tweet.component'




@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
  providers:[
    TweetService,
    UserService,
    FollowingService,
    FollowerService
  ]
})
export class SearchComponent implements OnInit {

  isPostClicked:boolean=false;
  isPeopleClicked:boolean=true;
  UserId:number;
  tweetList:Tweet[];
  peopleList:User[];
  followingList:User[];
  followersCount:number;
  followingCount :number;
  followerList:User[];
  searchvalue:string;
  likedDislikedTweetData:Tweetlikedislike;
  tweet:Tweet;

  constructor(private router:Router, private tweetservice:TweetService, private userservice:UserService,
    private followingservice:FollowingService, private followerservice:FollowerService, private changeDetectorRef: ChangeDetectorRef
    ,public dialog: MatDialog) { 
     if (sessionStorage.length <= 0)
     {
       this.router.navigateByUrl('/login');
     }
     this.searchvalue="";
     this.peopleList=[];
     this.followerList=[];
     this.followingList=[];
     this.followersCount=0;
    this.followingCount=0;

    this.likedDislikedTweetData=new Tweetlikedislike();
    this.tweet=new Tweet();
   
  }

  ngOnInit() {
    this.searchvalue="";
    this.UserId=parseInt(sessionStorage.getItem('sessionuserid'))
    this.tweetservice.getTweetById(this.UserId,this.searchvalue)
    .subscribe((event: any) => {
      this.tweetList=event
      
      
    
    });
    this.userservice.getAllUsers(this.UserId,this.searchvalue)
    .subscribe((event: any) => {
      this.peopleList=event
    });

    this.followingservice.getFollowingById(this.UserId,this.searchvalue)
  .subscribe((event: any) => {
   
    this.followingList=event
    this.followingCount=this.followingList.length

  });

  this.followerservice.getFollowerById(this.UserId)
  .subscribe((event: any) => {
  
    this.followerList=event
    this.followersCount=this.followerList.length;

  });



  }

  postClicked()
  {
    this.isPostClicked=true;
    this.isPeopleClicked=false;
    this.UserId=parseInt(sessionStorage.getItem('sessionuserid'))
   
   // this.router.navigateByUrl('/search');

  }
  peopleClicked()
  {
    this.isPeopleClicked=true;
    this.isPostClicked=false;
  
   // this.router.navigateByUrl('/search');
 


  }

  unFollowById(idToUnfollow)
  {
   
    this.followingservice.unFollowById(this.UserId,idToUnfollow)
    .subscribe((event: any) => {
      this.userservice.getAllUsers(this.UserId,this.searchvalue)
    .subscribe((event: any) => {
      this.peopleList=event
    });

    this.followingservice.getFollowingById(this.UserId,this.searchvalue)
  .subscribe((event: any) => {
   
    this.followingList=event
    this.followingCount=this.followingList.length

  });

  this.followerservice.getFollowerById(this.UserId)
  .subscribe((event: any) => {
 
    this.followerList=event
    this.followersCount=this.followerList.length;

  });
      
      //this.router.navigateByUrl('/following');
    });
}

FollowById(idToFollow)
{
 
   const  usr:UserFollower=new  UserFollower();
   usr.UserId=this.UserId;
   usr.FollowerId=idToFollow
  
  this.followingservice.FollowById(usr)
  .subscribe((event:any)=>{
    this.userservice.getAllUsers(this.UserId,this.searchvalue)
    .subscribe((event: any) => {
      this.peopleList=event
    });

    this.followingservice.getFollowingById(this.UserId,this.searchvalue)
  .subscribe((event: any) => {
   
    this.followingList=event
    this.followingCount=this.followingList.length

  });

  this.followerservice.getFollowerById(this.UserId)
  .subscribe((event: any) => {
  
    this.followerList=event
    this.followersCount=this.followerList.length;

  });
      

  })
 

}

search(searchvalue)
{
 
 this.searchvalue=searchvalue
  this.tweetservice.getTweetById(this.UserId,searchvalue)
  .subscribe((event: any) => {
    this.tweetList=event
  });

  this.userservice.getAllUsers(this.UserId,searchvalue)
    .subscribe((event: any) => {
      this.peopleList=event
    });

    this.followingservice.getFollowingById(this.UserId,searchvalue)
  .subscribe((event: any) => {
    this.followingList=event
  });


}



likeTweet(TweetId)
{
 
 this.likedDislikedTweetData.TweetId=TweetId;
 this.likedDislikedTweetData.LikedDislikedBy=this.UserId;
 this.likedDislikedTweetData.LikeDislike=true;
 this.tweetservice.tweetLikeClicked(this.likedDislikedTweetData).subscribe((event:any)=>{
  this.tweetservice.getTweetById(this.UserId,this.searchvalue)
    .subscribe((event: any) => {
      this.tweetList=event
    });
})
}
disLikeTweet(TweetId)
{

 
  this.likedDislikedTweetData.TweetId=TweetId;
  this.likedDislikedTweetData.LikedDislikedBy=this.UserId;
  this.likedDislikedTweetData.LikeDislike=false;
  this.tweetservice.tweetDisLikeClicked(this.likedDislikedTweetData).subscribe((event:any)=>{
    this.tweetservice.getTweetById(this.UserId,this.searchvalue)
    .subscribe((event: any) => {
      this.tweetList=event
    }); 
 })

}


createTweet()
  {
   
     let dialogRef = this.dialog.open(TweetComponent, {
      height: '300px',
      width: '250px',
      position: { bottom: '30' ,left:'50'},
      data: {  body: this.tweet.Body,
                type:"create" }
    });
  
    this.tweetservice.getTweetById(this.UserId,this.searchvalue)
    .subscribe((event: any) => {
      this.tweetList=event
      this.changeDetectorRef.detectChanges();  
    
    });
    
}
editTweet(tweetId:number)
{
  var  item=this.tweetList.find(item => item.Tid === tweetId);
 
  let dialogRef = this.dialog.open(TweetComponent, {
    height: '300px',
    width: '250px',
    position: { bottom: '30' ,left:'50'},
    data: {  body:item.Body, Tid:item.Tid ,
    type:"edit"}
  });
  dialogRef.afterClosed().subscribe(result => {
    this.tweetservice.getTweetById(this.UserId,this.searchvalue)
      .subscribe((event: any) => {
        this.tweetList=event
      
      });

})
   
}

deleteTweet(tweetId:number)
{
  this.tweetservice.deleteTweet(this.UserId,tweetId).subscribe((event:any)=>{
    alert("Your Tweet is Deleted..")
    this.tweetservice.getTweetById(this.UserId,this.searchvalue)
    .subscribe((event: any) => {
      this.tweetList=event
      
    
    });
  })
 }

logout()
{
  if(sessionStorage.length>=0)
  {
    sessionStorage.clear();
    window.alert("Successfully Logged Out");
        this.router.navigateByUrl('/login');
  }
}
}



